/********/
/* CARD */
/********/

.contentBox h2, .contentBox h3, .contentBox .buy {
    margin: 0;
    padding: 0;
    font-family: "Istok Web", sans-serif;
}

.contentBox h3{
    text-align: center;
    width: 80%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.card {
    position: relative;
    width: 320px;
    height: 480px;
    background: #191919;
    border-radius: 20px;
    overflow: hidden;
    margin: 1rem;
}

.card::before {
    content: "";
    position: absolute;
    top: -50%;
    width: 100%;
    height: 100%;
    background: #747474;
    -webkit-transform: skewY(345deg);
            transform: skewY(345deg);
    transition: 0.5s;
}

.card:hover::before {
    top: -70%;
    -webkit-transform: skewY(390deg);
            transform: skewY(390deg);
}

.card::after {
    content: "CharlyMan√≠a";
    position: absolute;
    bottom: 0;
    left: 0;
    font-weight: 600;
    font-size: 3em;
    color: rgba(126, 126, 126, 0.068);
}

.card .imgBox {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
    z-index: 1;
}

.mouse {
    height: 17rem;
    width: auto;
}
/* .card:hover .imgBox img {
    max-width: 75%;
    max-height: 250px ;
    transition: ease-in-out .5s;
    
}  */
.card .imgBox img {
    transition: ease-in-out .5s;
}


.card .contentBox {
    position: relative;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 2;
}

.card .contentBox h3 {
    font-size: 18px;
    color: white;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.card .contentBox .price {
    font-size: 24px;
    color: white;
    font-weight: 700;
    letter-spacing: 1px;
}

.card .contentBox .buy {
    position: relative;
    top: 100px;
    opacity: 0;
    padding: 10px 30px;
    margin-top: 15px;
    color: #000000;
    text-decoration: none;
    background: rgb(6, 168, 6);
    border-radius: 30px;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: 0.5s;
}

.card:hover .contentBox .buy {
    top: 0;
    opacity: 1;
}



.info-producto {
    line-height: .2rem;
}

.nombre-producto {
    font-weight: 700;
    margin-bottom: 2rem;
}

.link-ver-mas {
    border: 1px solid black;
    background-color: rgba(148, 137, 137, 0.897);
    color: white;
}


/*ItemDetail*/
.detalle-producto {
    height: 70vh;
    width: 90vw;
    margin: 1rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.detalle-producto .img-producto {
    width: 30rem;
}


/* Spinner */
.spinner {
    border: 4px solid rgba(0, 0, 0, 0.1);
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border-left-color: #09f;
    margin: 10% auto auto auto;


    -webkit-animation: spin 1s ease infinite;


            animation: spin 1s ease infinite;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

/* CART */

.tarjeta_cart {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 1rem;
    border-bottom: 1px solid grey;
    margin: auto 3rem;
}

.tarjeta_cart * {
